import React, { useState } from "react";
import { Link } from "react-router-dom"; // Asegrate de tener React Router instalado
import styles from "../../styles/User.module.css";

function Login() {
  const [usuario, setUsuario] = useState("");
  const [contrasena, setContrasena] = useState("");
  const [error, setError] = useState("");
  const [estaLogueado, setEstaLogueado] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();

    // Validacin bsica
    if (usuario === "" || contrasena === "") {
      setError("Usuario o contrasea vacos");
      return;
    }

    // Simular la autenticacin con un API (reemplazar con su lgica)
    if (usuario === "usuario" && contrasena === "contrasena") {
      setEstaLogueado(true);
      setError("");
    } else {
      setError("Usuario o contrasea incorrectos");
    }
  };

  const handleLogout = () => {
    setEstaLogueado(false);
  };

  return (
    <div className={styles.container}>
      {estaLogueado ? (
        <div className={styles.loggedIn}>
          <p>Bienvenido, {usuario}!</p>
          <button onClick={handleLogout}>Cerrar sesin</button>
        </div>
      ) : (
        <form onSubmit={handleSubmit} className={styles.loginForm}>
          <label htmlFor="usuario">Usuario:</label>
          <input
            type="text"
            id="usuario"
            placeholder="Introduzca Usuario"
            name="usuario"
            value={usuario}
            onChange={(e) => setUsuario(e.target.value)}
            required
          />
          <br />
          <label htmlFor="contrasena">Contrasea:</label>
          <input
            type="password"
            id="contrasena"
            placeholder="Introduzca Contrasea"
            name="contrasena"
            value={contrasena}
            onChange={(e) => setContrasena(e.target.value)}
            required
          />
          <br />
          <br />
          <div className={styles.divButton}>
            <button type="submit">Iniciar sesin</button>
            <Link to="/registro">
              <button type="button">Registrarse</button>
            </Link>
          </div>
          {error && <p className={styles.error}>{error}</p>}
        </form>
      )}
    </div>
  );
}

export default Login;
