import React, { useEffect, useState } from "react";
import axios from "axios";
import styles from "../styles/Forecast.module.css";
import startCase from "lodash/startCase";

const Forecast = ({}) => {
  const [data, setData] = useState({});
  const [location, setLocation] = useState("");
  const [weatherData, setWeatherData] = useState(null);
  const [error, setError] = useState("");
  const [icon, setIcon] = useState("");
  const [currentDateTime, setCurrentDateTime] = useState(new Date());

  const url = `https://api.openweathermap.org/data/2.5/weather?q=${location}&units=metric&lang=es&appid=10febab50520dc67582eed976e016d80`;

  useEffect(() => {
    // Funcin para actualizar la hora cada segundo
    const interval = setInterval(() => {
      setCurrentDateTime(new Date());
    }, 1000);

    // Limpieza del intervalo al desmontar el componente
    return () => clearInterval(interval);
  }, []);

  const searchLocation = async (event) => {
    if (event.key === "Enter") {
      try {
        const response = await axios.get(url);
        setData(response.data);
        console.log(response.data);
        setLocation("");
        setIcon(response.data.weather[0].icon);
        if (error) setError("");
      } catch (error) {
        console.error(error);
        setWeatherData(null);
        setData("");
        setIcon("");
        setError(
          "Ciudad incorrecta. Verifica el nombre de la ciudad e intntalo de nuevo."
        );
      }
    }
  };

  const zonaHoraria = data.timezone / 3600; // La zona horaria est en segundos, convierte a horas
  const fechaLocal = new Date(Date.now() + zonaHoraria * 60 * 60 * 1000);

  // Obtener el nombre del da de la semana actual en el formato deseado
  const dayOfWeek = fechaLocal.toLocaleString("es-ES", { weekday: "long" });

  return (
    <div className={styles.forecastCont}>
      <div className={styles.div1}>
        <div className={styles.div1_1}>
          <div className={styles.div1_1_1}>
            <h2>Buenos das Luis</h2>
            <div className="search">
              <input
                value={location}
                onChange={(event) => setLocation(event.target.value)}
                onKeyPress={searchLocation}
                placeholder={
                  data && data.name ? data.name : "Introduzca ciudad"
                }
                type="text"
              />
              {error && <p style={{ color: "red" }}>{error}</p>}
            </div>
          </div>
          <div className={styles.divDate}>
            {
              <h2>
                {data.timezone
                  ? currentDateTime.toLocaleTimeString("es-ES", {
                      hour: "2-digit",
                      minute: "2-digit",
                      hour12: false,
                    })
                  : "No hay datos que mostrar "}
              </h2>
            }
            <h4>
              {data.timezone
                ? `${startCase(dayOfWeek)}, ${fechaLocal.toLocaleDateString()}`
                : ""}
            </h4>
          </div>
          <div className={styles.divWeather}>
            <h4>Pronstico del Tiempo</h4>
            <div className={styles.description}>
              <div className={styles.divWeather}>
                {data.weather ? <p>{startCase(data.weather[0].main)}</p> : null}
                {data.weather ? (
                  <p>{startCase(data.weather[0].description)}</p>
                ) : null}
              </div>
              {icon && (
                <img
                  src={`https://openweathermap.org/img/wn/${icon}@2x.png`}
                  alt="iconWeather"
                />
              )}
            </div>
          </div>
        </div>
        <div className={styles.div1_2}>B</div>
        <div className={styles.div1_3}>C</div>
      </div>
      <div className={styles.div2}>
        <div className={`${styles.div1_1} ${styles.div1_1_right}`}>
          {icon && (
            <img
              src={`https://openweathermap.org/img/wn/${icon}@2x.png`}
              alt="iconWeather"
            />
          )}
          <div className={styles.temp}>
            {data.main ? <h3>{data.main.temp.toFixed()}C</h3> : null}
            {data.weather ? <p>{startCase(data.weather[0].main)}</p> : null}
            {data.wind ? (
              <div className={styles.wind}>
                <p>
                  <i className="fas fa-wind"></i> Viento
                </p>
                <p style={{ width: "3px" }}>|</p>
                {data.wind ? (
                  <p className={styles.bold}>{data.wind.speed.toFixed()} MPH</p>
                ) : null}
              </div>
            ) : null}
            {data.wind ? (
              <div className={styles.wind}>
                <p>
                  <i className="fa-solid fa-water"></i> Humedad
                </p>
                <p style={{ width: "3px" }}>|</p>
                {data.wind ? (
                  <p className={styles.bold}>{data.main.humidity}%</p>
                ) : null}
              </div>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
};
export default Forecast;
